.FinishedQuiz {
    padding: 20px;
    color: #fff;
    border: 2px solid #fff;
    border-radius: 5px;
    box-sizing: border-box;
    margin: 0 10px;
    background-color: rgba(41, 40, 40, 0.788);
    box-shadow: 7px 10px 10px rgba(80, 75, 75, 0.788);
}

.FinishedQuiz ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.FinishedQuiz ul li i {
    margin-left: 10px;
}

.success {
    color: rgba(148, 202, 87, 0.7);
}

.error {
    color: rgba(226, 24, 51, 0.7);
}